* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Karla',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

main {
  padding: 36px;
}

.header {
  background-image: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
  color: white;
  display: flex;
  align-items: center;
  padding: 20px;
}

.header-img {
  height: 35px;
  margin-right: 6px;
}

.header-title {
  font-family: 'Karla', sans-serif;
  font-size: 1.25rem;
  letter-spacing: -0.1em;
  margin-right: auto;
}

.header-p {
  font-family: 'Inter', sans-serif;
  font-size: 0.75rem;
  font-weight: 500;
}

.form {
  display: grid;
  grid-template: 40px 40px / 1fr 1fr;
  gap: 17px;
}

.form-input {
  font-family: "Karla", sans-serif;
  border-radius: 5px;
  border: 1px solid #D5D4D8;
  text-indent: 5px;
}

.form-btn {
  grid-column: 1/ -1;
  font-family: "Karla", sans-serif;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-image: linear-gradient(90.41deg, #711F8D 1.14%, #A818DA 100%);
}

.meme {
  position: relative;
}

.meme-img {
  max-width: 100%;
  padding-top: 10px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}

.meme--text {
  position: absolute;
  width: 80%;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  margin: 15px 0;
  padding: 0 5px;
  font-family: impact, sans-serif;
  font-size: 2em;
  text-transform: uppercase;
  color: white;
  letter-spacing: 1px;
  text-shadow:
      2px 2px 0 #000,
      -2px -2px 0 #000,
      2px -2px 0 #000,
      -2px 2px 0 #000,
      0 2px 0 #000,
      2px 0 0 #000,
      0 -2px 0 #000,
      -2px 0 0 #000,
      2px 2px 5px #000;
}

.bottom {
  bottom: 0;
}

.top {
  top: 0;
}